import { buildSpeech as eBuildSpeech } from "@nmemonica/voice-en";
import { exceptionToErrorObj } from ".";
const wSelf = globalThis.self;
wSelf.addEventListener("message", messageHandler);
function messageHandler(event) {
    const data = event.data;
    const { uid, index, tl: language, q: query, AbortController, englishVoice } = data;
    if (language === "en" && query !== null && typeof eBuildSpeech === "function") {
        let voice_model;
        switch(englishVoice){
            case "default":
            case undefined:
                voice_model = "Robot Male";
                break;
            default:
                voice_model = englishVoice;
        }
        try {
            const { uid: resUid, index: resIndex, buffer: resBuffer } = eBuildSpeech(uid, index, query, voice_model);
            const response = {
                uid: resUid,
                index: resIndex,
                buffer: resBuffer
            };
            wSelf.postMessage(response);
        } catch (exception) {
            const error = exceptionToErrorObj(exception, "voice-worker-en");
            wSelf.postMessage(error);
        }
    }
}
